  >
    <p class="text-lg mb-2.5 truncate font-medium">{{ dataSource.title }}</p>
    <p class="text-base mb-1.5 truncate">{{ dataSource.description }}</p>
    <div class="flex justify-between items-center">
      <div class="text-sm">
        <span> 分類: {{ dataSource.type }} </span>
        <span class="ml-4"> 總點擊數: {{ dataSource.num }} </span>
      </div>
      <div
        v-if="dataSource.onlyMember"
        class="rounded w-24 py-1 flex items-center justify-center text-white bg-main-red"
      >
        會員限定
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    dataSource: {
      type: Object,
      default: () => ({}),
    },
  },
  methods: {
    handleClickCard() {
      const { id, path } = this.dataSource;
      this.$router.push(`${path}/detail/${id}`);
    },
  },
};
</script>
