<script>
import MessageBoard from "@/components/MessageBoard";
import DetailOperation from "@/components/DetailOperation";
import DetailTags from "@/components/DetailTags";
import NovelCard from "@/components/NovelCard";
import ChangePageComponent from "@/components/ChangePageComponent";
import mobileMixin from "../../mixin/mobileMixin";
import { novels } from "../../fakeData";

const fakeNovel = novels.slice(0, 6).map((item) => ({
  ...item,
  path: "/novel",
}));
const mobileFakeNovel = novels
  .slice(0, 4)
  .map((item) => ({
    ...item,
    path: "/novel",
  }))
  .reduce(
    (acc, item, index) => {
      if ((index + 1) % 1 === 0) {
        return [
          ...acc,
          item,
          {
            type: "ad",
          },
        ];
      }
      return [...acc, item];
    },
    [
      {
        type: "ad",
      },
    ]
  );
export default {
  mixins: [mobileMixin],
  components: {
    MessageBoard,
    DetailOperation,
    DetailTags,
    NovelCard,
    ChangePageComponent,
  },
  data() {
    return {
      totalPage: 10,
      currentPage: 1,
      dataSource: fakeNovel,
    };
  },
  methods: {
    changePage(page) {
      this.currentPage = page;
    },
    handleAddAds() {
      this.dataSource = mobileFakeNovel;
    },
    handleRemoveAds() {
      this.dataSource = fakeNovel;
    },
  },
  watch: {
    isMobile: {
      immediate: true,
      handler(newValue, oldValue) {
        if (newValue && !oldValue) {
          this.handleAddAds();
        } else {
          this.handleRemoveAds();
        }
      },
    },
  },
};
</script>
