const mobileMixin = {
  data() {
    return {
      windowWidth: "",
    };
  },
  computed: {
    isMobile() {
      return this.windowWidth < 1024;
    },
  },
  mounted() {
    this.$nextTick(() => {
      window.addEventListener("resize", this.onResize);
    });
    this.onResize();
  },

  beforeDestroy() {
    window.removeEventListener("resize", this.onResize);
  },

  methods: {
    onResize() {
      this.windowWidth = window.innerWidth;
    },
    addAds(array, gap = 4) {
      return array.reduce((acc, item, index) => {
        if ((index + 1) % gap === 0) {
          return [
            ...acc,
            item,
            {
              type: "ad",
            },
          ];
        }
        return [...acc, item];
      }, []);
    },
    removeAds(array) {
      return array.filter((item) => item.type !== "ad");
    },
  },
};

export default mobileMixin;
